.form {
  display: flex;
  flex-direction: column;
}

.form__wrapper {
  width: 280px;
  margin: 0 auto;
}

/* Представьтесь*/
.form__form-name {
  display: flex;
  flex-direction: column;
  margin-top: 53px;
  margin-bottom: 64px;
}

/* Контактная информация*/
.form__form-contacts {
  display: flex;
  flex-direction: column;
  margin-bottom: 59px;
}

.form-phone__wrapper {
  display: flex;
  flex-direction: row;
}

.form-phone__img {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 16px;
  background-color: rgba(230, 230, 230, 1);
}

.form-email__wrapper {
  display: flex;
  flex-direction: row;
}

.form-email__img {
  padding: 16px;
  background-color: rgba(230, 230, 230, 1);
  display: flex;
  align-items: center;
  box-sizing: border-box;
}

/*Ваше общее впечатление*/
.form__form-impression {
  display: flex;
  flex-direction: column;
  margin-bottom: 49px;

  .form__fieldset-group {
    border: none;
  }

  .form__fieldset-item {
    padding-bottom: 16px;
    font-family: inherit;
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    text-transform: none;
  }

  .form__fieldset-item:last-child {
    padding-bottom: 0;
  }

  .control {
    position: relative;
    display: block;
    padding: 10px 10px 0 54px;
  }

  .control-mark {
    position: absolute;
    top: 2px;
    left: -13px;
    width: 46px;
    height: 46px;
    background-color: rgba(242, 242, 242, 1);
    border-radius: 50%;
    margin-bottom: 16px;
  }

  .control-mark:last-child {
    margin-bottom: 0;
  }

  .control-input[type="radio"]:checked + .control-mark::before {
    position: absolute;
    top: 17px;
    left: 17px;
    width: 10px;
    height: 10px;
    background-color: rgba(51, 51, 51, 1);
    border-radius: 50%;
    content: "";
  }

  .control-input:hover + .control-mark {
    background-color: #e5e5e5;
  }

  .control-input:active + .control-mark {
    background-color: rgba(213, 213, 213, 1);
  }
}

/*Посещенные достопримечательности*/
.form__form-attractions {
  display: flex;
  flex-direction: column;
  margin-bottom: 49px;

  .form__fieldset-group {
    border: none;
  }

  .form__fieldset-item {
    padding-bottom: 16px;
    font-family: inherit;
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    text-transform: none;
  }

  .form__fieldset-item:last-child {
    padding-bottom: 0;
  }

  .control {
    position: relative;
    display: block;
    padding: 10px 10px 0 54px;
  }

  .control-mark {
    position: absolute;
    top: 2px;
    left: -13px;
    background-color: #f2f2f2;
    border-radius: 4px;
  }

  .control-input[type="checkbox"]:checked + .control-mark::before {
    position: absolute;
    top: 17px;
    left: 17px;
    content: url("../img/form-tick.svg");
  }

  .control-input:hover + .control-mark {
    background-color: #e5e5e5;
  }

  .control-input:active + .control-mark {
    background-color: rgba(213, 213, 213, 1);
  }
}

/*Опишите свои эмоции*/
.form__form-emotions {
  display: flex;
  flex-direction: column;
  margin-bottom: 56px;
}

.form__form-emotions textarea {
  min-height: 150px;
  background-color: rgba(242, 242, 242, 1);
  color: rgba(0, 0, 0, 0.3);
  padding: 16px;
  font-family: inherit;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  border: none;
}

.form-emotions__text {
  color: rgba(51, 51, 51, 0.6);
  margin-left: auto;
  margin-top: 3px;
}

.form__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  padding: 0;
  margin: 0;
  padding-bottom: 20px;
}

.field {
  padding: 16px;
  padding-right: 0;
  background-color: rgba(242, 242, 242, 1);
  border: none;
  font-family: inherit;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  box-sizing: border-box;
}

.field:last-child {
  margin-bottom: 0;
}

.field:hover,
.field:focus {
  background-color: rgba(229, 229, 229, 1);
  color: #000000;
}

.field:active {
  background-color: rgba(255, 255, 255, 1);
  outline: 4px solid rgba(229, 229, 229, 1);
  color: #000000;
}

.form__button {
  min-height: 59px;
  box-sizing: border-box;
}

